.Work{
    width: 80%;
    margin: 0 auto;
    margin-bottom: 2%;
    padding: 0;
    height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
}
.Work-head{
    flex: 0.15;
    width: 100%;
    font-family: 'Ubuntu', sans-serif;
    color: var(--accent-default);
    font-size: 2.5rem;
    font-weight: 600;
    letter-spacing: 2px;
    word-spacing: 5px;
}
.Work-list{
    flex: 0.7;
    width: 90%;
    /* justify-self: start; */
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}
.Work-col {
    height: 100%;
    flex: 0.3;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.Work-col:nth-child(2){
    flex-direction: column-reverse;
}
.Work-name{
    font-size: 1.5rem;
    font-family: 'Play',sans-serif;
    color: var(--font-default);
}

.Work-all{
    width: 100%;
    flex: 0.1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Work-all > #work-link {
    display: inline-block;
    text-decoration: none;
    font-weight: 600;
    letter-spacing: 2px;
    font-size: 1.3rem;
    margin-left: 5px;
    color: var(--accent-blue);
    font-family: "Space Mono",monospace;
    position: relative;
}
.Work-all > #work-link:hover {
    cursor: pointer;
}
.Work-all > #work-link::after{
    content: "";
    display: block;
    width: 0;
    height: 1px;
    background-color: var(--accent-default);
    position: relative;
    bottom: 0;
    transition: 300ms all ease-in-out;
}
.Work-all > #work-link:hover::after{
    width: 100%;
}
