.Social {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    width: 100%;
    margin: 20px 0;
    transition: all 300ms ease-in-out;
}
.Social-icon {
    flex: 0.2;
    object-fit: contain;
    height: 100%;
}
.Social:hover{
    transform: scale(1.15);
    cursor: pointer;
}