.WorkCard {
    width: 95%;
    max-width: 210px;
    height: 85%;
    max-height: 250px;
    position: relative;
    overflow: hidden;
    background: linear-gradient(145deg, #010a12, #010c15);
    box-shadow:  12px 12px 0px #01070c,
             -12px -12px 0px #031424;
    border-radius: 5px;
    transition: all 200ms ease-in-out;
}
.WorkCard:hover{
    cursor: pointer;
    transform: scale(1.02);
    background: linear-gradient(145deg, #010a12, #010c15);
    box-shadow:  25px 25px 0px #000508,
             -25px -25px 0px #031424;

}
.WorkCard-img{
    position: absolute;
    width: 100%;
    height: 100%;
}
.WorkCard-img>img{
    width: 100%;
    object-fit: contain;
}